.updraftcentral_row_extracontents .expertmode .advanced_settings_container {

	height: auto;
	overflow: hidden;

	.advanced_settings_menu {
		border-bottom: 1px solid rgb(204, 204, 204);
		/*
		This is here so that there isn't an ugly space between tabs
		*/
		font-size: 0px;

		.advanced_tools_button {
			display: inline-block;
			font-size: 16px;
			cursor: pointer;
			padding: 5px;
			color: #000000;
		}

		.advanced_tools_button:hover {
			background-color: #BF5519;
			color: #ffffff;
		}

		.advanced_tools_text {
			font-size: 16px;
			vertical-align: middle !important;
		}

		.active {
			background-color: #BF5519;
			color:#ffffff;
		}
	}

	.advanced_settings_content {
		padding-top: 5px;
		font-size: 16px;
		width:auto;

		h3 {
			margin-top: 5px !important;
		}

		h4 {
			padding: 5px 0px;
		}

		.advanced_tools {
			display: none;
		}

		.site_info {
			display: block;
		}

		input#import_settings {
			height:auto !important;
		}

		button, input[type="submit"] {
			border: 1px solid transparent;
			-webkit-user-select: none;
			padding: .25rem .5rem;
			font-size: .875rem;
			border-radius: .2rem;
			border: 1px solid #269cdf;
			color: #ffffff !important;
			background-color: #269cdf;
		}

		button:hover, input[type="submit"]:hover,
		button:active, input[type="submit"]:active,
		button:focus, input[type="submit"]:focus {
			border-color: transparent !important;
			color: #ffffff !important;
			background-color: #1b7fb7;
			text-decoration: none !important;
		}
	}

	.updraft_debugrow th {
		float: right;
		text-align: right;
		font-weight: bold;
		padding-right: 8px;
		min-width: 140px;
	}

	.updraft_debugrow td {
		min-width: 300px;
	}

	#search_replace_form input[type="text"], #search_replace_form input[type="number"], #lock_form input[type="text"], #lock_form select {
		width:230px;
	}
	
	#updraftcentral_keys .create_key_container, #updraftcentral_keys .create_key_container, 
	#updraftcentral_keys .updraftcentral_key_delete {
		display: none; 
	}
}

